import React from 'react';
import { Link} from 'react-router-dom';

function Footer() {
  return (
    <section>
      <footer className='bg-dark mt-5 container-fluid footer pb-2'>
        <div className='footer pt-5'>
            <ul className='ul'>
                <li className='li'><h5 className='text-light pt-3'>Contactanos:</h5></li>
                <li className='li'><Link to='/'><img className='footer-img' src="https://cdn3.iconfinder.com/data/icons/social-icons-5/607/Twitterbird.png" alt="Twitter"/></Link></li>
                <li className='li'><Link to='/'><img className='footer-img' src="https://upload.wikimedia.org/wikipedia/en/thumb/8/8c/Facebook_Home_logo_old.svg/1024px-Facebook_Home_logo_old.svg.png" alt="Facebook"/></Link></li>
                <li className='li'><Link to='/'><img className='footer-img' src="https://freeiconshop.com/wp-content/uploads/edd/mail-flat.png" alt="Email"/></Link></li>
            </ul>   
        </div>
        <div>
            <p className='text-warning'>Created by: Ángel Ordóñez Sánchez (Nenu011) 2018.</p>
            <p className="text-muted">Las imágenes de los productos son solo demostrativas y los derechos son completamente del autor.</p>
        </div>
      </footer>
    </section>
  )
}

export default Footer;
