import React from 'react';
import {Link} from 'react-router-dom';
import '../App.css';

function Card(props) {
  return (
    <div class="card Card-sm mt-3" >
        <img class="card-img-top Card-image" 
        src={props.cover}
        alt='Imagen producto'
        />
        <div class="card-body">
            <h5 class="card-title">{props.title}</h5>
            <p class="card-text text-secondary p" >{props.description}</p>
            <Link to='/' className='btn btn-primary'>Ver</Link>
        </div>
    </div>
  )
}

export default Card;
