import React from 'react';
import { Link } from 'react-router-dom';

function Navbar() {
  return (
    <div>
        <nav className="navbar navbar-expand-lg navbar-light bg-light">
            
            <div className='mr-auto'>
                <ul className="navbar-nav">
                    <li className="nav-item">
                        <div class="btn-group">
                        <button type="button" class="btn btn-sm btn-primary dropdown-toggle" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                            Categorias
                        </button>
                        <div class="dropdown-menu">
                            <Link to='/' className='dropdown-item'>Mujer</Link>
                            <Link to='/' className='dropdown-item'>Hombre</Link>
                            <Link to='/' className='dropdown-item'>Niña</Link>
                            <Link to='/' className='dropdown-item'>Niño</Link>
                        </div>
                        </div>
                    </li>
                </ul>
                
            </div>
            <Link to='/' className="navbar-brand ">Nombre de tu empresa</Link>
        </nav>
    </div>
  )
}

export default Navbar;
